body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  box-sizing: border-box;
  font-size: 20px;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}
.Game {
    text-align: center;
}

.Game .status {
    margin-bottom: 10px;
    padding: 10px;
    background-color: #ccc;
}

.Game .restart {
    text-align: center;
    background: white;
    font-size: 26px;
    line-height: 32px;
}

.Square {
    width: 40px;
    height: 40px;
    font-size: 30px;
    font-weight: bold;
    line-height: 40px;
    background: #eee;
    border: 1px outset #ddd;
    cursor: default;
    display: inline-block;
    float: left;
    outline: none;
}
.Square.mine {
    background-color: darkred;
    color: white;
}
.Square.revealed {
    background: #ddd;
}
.Square.number1 {
    color: blue;
}
.Square.number2 {
    color: green;
}
.Square.number3 {
    color: red;
}
.Square.number4 {
    color: darkblue;
}
.Square.number5 {
    color: darkred;
}
.Square.number6 {
    color: teal;
}
.Square.number7 {
    color: #333;
}
.Square.number8 {
    color: deeppink;
}

.Board {
    display: inline-block;
    margin: 0 auto;
    border: 4px solid #ddd;
}
.row::after {
    clear: both;
    content: "";
    display: block;
}
